#root {
    width: 200px;
    margin: 40px auto;
}

.result{
    height: 40px;
    background: black;
}

.resultValue{
    text-align: end;
    height: 100%;
    font-size: 30px;
    color: white;
    padding-right: 15px; 
}

.wrapper{
    display: flex;
    justify-content: center;
    background: black;
}

button {
    border-radius: 50%;
    width: 40px;
    height: 40px;
    margin: 5px;
    background: #555555;
    border: none;
    color: white;
    font-size: 25px;
    padding: 0;
  }


  button:hover {
    background: #999999;
  }

  button:focus {
    outline: none;
  }

  .operator button {
    background: orange;
  }

  .operator button:hover{
    background: #FFC7AF;
  }

  .operator button:focus {
    background: white;
    color: orange;
  }

  .operator {
    display: flex;
    flex-direction: column;
  }